<template>
  <Card cardDirection="column">
    <ProductLayout>
      <section>
        <div class="product__type">
          <Title>服務建議</Title>
          <SuggestButton v-for="(item, idx) in serviceTable" :key="`service-${idx}`" :state="item.state">
            <router-link :to="`./suggest?id=${item.id}`"> {{ item.title }} </router-link>
          </SuggestButton>
        </div>
        <div class="product__type">
          <Title>商機推薦</Title>
          <SuggestButton v-for="(item, idx) in productTable" :key="`product-${idx}`" :state="item.state">
            <router-link :to="`./suggest?id=${item.id}`"> {{ item.title }} </router-link>
          </SuggestButton>
        </div>
      </section>
      <MoreButton align="center" @click="showMore">
        <p>看更多選項</p>
        <Icon :iconUrl="require('../assets/button_more.svg')" :size="20" :rotate="90" />
      </MoreButton>
    </ProductLayout>
    <InsuranceLayout>
      <div>
        <Title>保障缺口</Title>
        <ButtonWrapper>
          <SwitchButton :word="isSync == 'on' ? '同步中 ●' : '同步畫面'">
            <input type="checkbox" v-model="isSync" value="on"/>
            <span class="slider"></span>
          </SwitchButton>    
          <div>
            <DropDownButton @click="isConOpen = !isConOpen">
              <span>配對顧問</span>
              <Icon v-if="!isConOpen" :iconUrl="require('../assets/icon-arrow-white-down.svg')" />
              <Icon v-if="isConOpen" :iconUrl="require('../assets/icon-arrow-white-up.svg')" />
            </DropDownButton>
            <DropDown v-if="isConOpen" @click="isConOpen = !isConOpen">
              <router-link to="./introduce?type=current&cat=consult">
                <LinkStyle textColor="#324c5a">原顧問</LinkStyle>
              </router-link>
              <router-link to="./introduce?type=new&cat=consult">
                <LinkStyle textColor="#324c5a">新顧問</LinkStyle>
              </router-link>
            </DropDown>
          </div>
          <div>
            <DropDownButton @click="isCusOpen = !isCusOpen">
              <span>諮詢客服</span>
              <Icon v-if="!isCusOpen" :iconUrl="require('../assets/icon-arrow-white-down.svg')" />
              <Icon v-if="isCusOpen" :iconUrl="require('../assets/icon-arrow-white-up.svg')" />
            </DropDownButton>
            <DropDown v-if="isCusOpen" @click="isCusOpen = !isCusOpen">
              <router-link to="./introduce?type=current&cat=service">
                <LinkStyle textColor="#324c5a">客服顧問</LinkStyle>
              </router-link>
              <router-link to="./introduce?type=new&cat=service">
                <LinkStyle textColor="#324c5a">本次客服</LinkStyle>
              </router-link>
            </DropDown>
          </div>
        </ButtonWrapper>
      </div>
      <InsuranceSection :insurances="insurances" :class="`${isSync == 'on' ? 'active' : ''}`" />
      <div class="suggest__notime">
        全部沒時間
        <div>-</div>
      </div>
    </InsuranceLayout>
  </Card>
</template>
 
<script>
import { 
  Card, 
  Title, 
  SuggestButton, 
  DropDownButton,
  MoreButton, 
  Icon,
  SwitchButton, 
  ButtonWrapper, 
  DropDown,
  LinkStyle } from '../style.js';
import InsuranceSection from './insuranceSection.vue';
import styled from 'vue-styled-components';

const ProductLayout = styled.section`
  padding: 10px 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: 0 6px 15px 0 rgba(128, 197, 197, 0.72);
  overflow: scroll;
  a {
    color: white;
    &:hover {
      color: white;
      text-decoration: none;
    } 
  }
  section {
    display: flex;
    justify-content: space-between;
  }
  .product__type {
    display: flex; 
    flex-direction: column;
  }
`

const InsuranceLayout = styled.section`
  padding: 30px;
  & > div {
    display: flex;
    align-items: center;
    & > div {
      margin-left: 25px;
    }
  }
  section {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 20px;
    border-radius: 12px;
    padding: 5px;
    &.active {
      box-shadow: 0 0 0 5px #ffcbcb, 0 0 0 10px #feecec;
    }
  }
  .suggest__notime {
    position: absolute; 
    bottom: -25px;
    left: 50%; 
    transform: translateX(-50%);
    color: white;
    background: #616161;
    border-radius: 20px;
    font-size: 16px; 
    padding: 2px 10px;
    div {
      background: rgba(255, 255, 255, 0.2);
      border-radius: 10px;
      width: 20px;
      height: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
`

export default {
  name: 'SuggestSection',
  components: {
    Card,
    Title,
    ProductLayout,
    SuggestButton,
    InsuranceLayout,
    DropDownButton,
    InsuranceSection,
    MoreButton,
    Icon,
    SwitchButton,
    ButtonWrapper,
    DropDown,
    LinkStyle
  },
  methods: {
    showMore() {
      this.serviceTable = this.menuState ? this.service.slice(0, 2) : this.service;
      this.productTable = this.menuState ? this.product.slice(0, 2) : this.product;
      this.menuState = !this.menuState;
    }
  },
  props: {
    service: Array,
    product: Array,
    insurances: Array
  },
  data() {
    return {
      isConOpen: false,
      isCusOpen: false,
      menuState: false,
      isSync: [],
      serviceTable: [],
      productTable: [],
    }
  },
  mounted: function(){
    this.serviceTable = this.service.slice(0, 2);
    this.productTable = this.product.slice(0, 2);
  }
}
</script>
